a{
    text-decoration: none;
    color: inherit;
}

.movie img{
    position: relative;
    top: -3vw;
    max-width: 7vw;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
                0 18px 36px -18px rgba(0, 0, 0, 0.3),
                0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year{
    margin: 0;
    font-weight: 300;
}

.movie .movie__title{
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
    transition: all .3s ease-in-out;
}

.movie .movie__title:hover{
    color : black;
    font-weight: 500;
}

.movie .movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px 0px;
}

.movie__genres li,
.movie .movie__year {
    margin-right: 10px;
    font-size: 14px;
}